import styled from '@emotion/styled';
import { useState } from 'react';
import { useNavigate, useSearchParams } from 'react-router-dom';

import { BASE_URL, fetchInstance } from '@/api/instance';
import KAKAO_LOGIN from '@/assets/kakao_login_large_wide.png';
import KAKAO_LOGO from '@/assets/kakao_logo.svg';
import { Button } from '@/components/common/Button';
import { UnderlineTextField } from '@/components/common/Form/Input/UnderlineTextField';
import { Spacing } from '@/components/common/layouts/Spacing';
import { breakpoints } from '@/styles/variants';
import { authSessionStorage } from '@/utils/storage';

export const LoginPage = () => {
  const [email, setEmail] = useState('');
  const [password, setPassword] = useState('');
  const [queryParams] = useSearchParams();
  const navigate = useNavigate();

  const exampleCode = 'exampleCode';

  const handleLogin = async () => {
    const registerUser = authSessionStorage.get();

    //    
    if (registerUser){
      //   ,   
      if (registerUser && email === registerUser.email && password === registerUser.password) {
        try {
          // : fetch   :  instance 
          const response = await fetchInstance.post(`${BASE_URL}/api/members/login`, {
            email: email,
            password: password,
          });
          
          //   (200) :    
          if (response.status === 200) {
            const data = await response.data;
            authSessionStorage.set({ email: email, token: data.token });
            const redirectUrl = queryParams.get('redirect') ?? `${window.location.origin}/`;
            return window.location.replace(redirectUrl);
          }
          
          //   (400) :  
          else if (response.status === 400) {
            const data = await response.data;
            console.error(data.message);
            alert(' .');
          }
        } catch (error) {
          console.error(error);
          alert('   .');
        }
      }
    } else {
      //    
      alert('    .');
    }
  }

  const handleKakaoLogin = async () => {
    try {
      const response = await fetchInstance.get(`${BASE_URL}/api/oauth2/kakao`, {
        params: {
          //    !
          code: exampleCode
        }
      });
      
      //    (200) :    
      if (response.status === 200) {
        const data = await response.data;
        authSessionStorage.set({ email: exampleCode, token: data.token });
        const redirectUrl = queryParams.get('redirect') ?? `${window.location.origin}/`;
        return window.location.replace(redirectUrl);
      }
    
      //    (400) :  
      else if (response.status === 400) {
        const data = await response.data;
        console.error(data.message);
        alert('  .');
      }
    } catch (error) {
      console.error(error);
      alert('    .');
    }
  }

  return (
    <Wrapper>
      <Logo src={KAKAO_LOGO} alt=" CI" />
      <FormWrapper>
        <UnderlineTextField placeholder="" value={email} onChange={(e) => setEmail(e.target.value)} />
        <Spacing />
        <UnderlineTextField
          type="password"
          placeholder=""
          value={password}
          onChange={(e) => setPassword(e.target.value)}
        />

        <Spacing
          height={{
            initial: 40,
            sm: 60,
          }}
        />
        <Button theme={'darkGray'} onClick={handleLogin}></Button>
        <Spacing height={20} />
        <Button theme={'darkGray'} onClick={() => navigate('/signup')}> </Button>
        <Spacing height={20} />
        <KakaoLoginButton onClick={handleKakaoLogin}>
          <KakaoLogo src={KAKAO_LOGIN} alt=" " />
        </KakaoLoginButton>
      </FormWrapper>
    </Wrapper>
  );
};

const Wrapper = styled.div`
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
`;

const Logo = styled.img`
  width: 88px;
  color: #333;
`;

const FormWrapper = styled.article`
  width: 100%;
  max-width: 580px;
  padding: 16px;

  @media screen and (min-width: ${breakpoints.sm}) {
    border: 1px solid rgba(0, 0, 0, 0.12);
    padding: 60px 52px;
  }
`;

const KakaoLoginButton = styled.button`
  background: none;
  border: none;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
`;

const KakaoLogo = styled.img`
  height: auto;
`;

